  position: relative;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 40vw;
  color: #000;
  text-align: justify;
  z-index: 1;
  opacity: 0;
  background-color: #fff;
  border-radius: 6px;
  padding: 10px;
  font-size: 1rem;
  transition: opacity .3s;
  position: absolute;
  top: 125%;
  left: calc(50% - 30vw);
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

.tooltip:hover .tooltip-icon {
